/* General styles for the form */
form {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    margin: 20px auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  input {
    margin-bottom: 15px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  button {
    padding: 10px;
    border: none;
    border-radius: 4px;
    background-color: #3bb19b;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color:  hsl(150, 9%, 22%);
  }
  
  select {
    margin-bottom: 15px; /* Same as input fields */
    padding: 10px; /* Consistent padding with inputs */
    border: 1px solid #ddd; /* Same border as inputs */
    border-radius: 4px; /* Same rounded corners as inputs */
    background-color: #fff; /* Matches input background */
    font-size: 14px; /* Matches font size */
    color: #7d7984; /* Matches input text color */
    width: 100%; /* Ensures dropdown matches input width */
    height: 40px; /* Matches input height */
}

select:focus {
    outline: none; /* Removes default outline */
    border-color: #000806; /* Matches focus color of inputs */
}
 
  /* Table styles */
  .table-container {
    margin: 20px auto;
    max-width: 800px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    overflow: hidden;
  }
  
  .table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .table th, .table td {
    padding: 15px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
  
  .table th {
    background-color: #f2f2f2;
    color: #333;
    font-weight: bold;
  }
  
  .table tr:hover {
    background-color: #f5f5f5;
  }
  
  .table .actions {
    display: flex;
    gap: 10px;
  }
  
  .table button {
    padding: 5px 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  