

:root {
	--header-bg: #2a2a2a;
	--header-text: #ffffff;       
	--primary: #140d07;           
	--primary-hover: #6F4E37;     
	--background: #121212;        
	--text-dark: #f5f5f5;         
	--text-medium: #e0e0e0;       
	--hero-bg: #1a1a1a;           
	--border-color: #333333;      
  }

  .homeBtn {
	background-color: white;
	color: black;
	border: none;
	padding: 0.6rem 1.5rem;
	border-radius: 8px;
	font-weight: 600;
	font-size: 1rem;
	cursor: pointer;
	transition: all 0.2s;

	
  }
  
  .homeBtn:hover {
	background-color: var(--primary-hover);
	transform: translateY(-1px);

  }
  
.login_container {
	width: 100%;
	min-height: 100vh;
	background-color: #f5f5f5;
	display: flex;
	align-items: center;
	justify-content: center;
	font-family: 'Arial', sans-serif;

	.login_container {
		width: 100%;
		min-height: 100vh;
		background-color: #f5f5f5;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	
	.login_form_container {
		width: 900px;
		height: 500px;
		display: flex;
		border-radius: 10px;
		box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 20%),
			0px 3px 4px 0px rgb(0 0 0 / 14%), 0px 1px 8px 0px rgb(0 0 0 / 12%);
	}
	
	.left {
		flex: 2;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		background-color: white;
		border-top-left-radius: 10px;
		border-bottom-left-radius: 10px;
	}
	
	.form_container {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	
	.form_container h1 {
		font-size: 40px;
		margin-top: 0;
	}
	
	.input {
		outline: none;
		border: none;
		width: 370px;
		padding: 15px;
		border-radius: 10px;
		background-color: #edf5f3;
		margin: 5px 0;
		font-size: 14px;
	}
	
	.error_msg {
		width: 370px;
		padding: 15px;
		margin: 5px 0;
		font-size: 14px;
		background-color: #f34646;
		color: white;
		border-radius: 5px;
		text-align: center;
	}
	
	.right {
		flex: 1;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		background-color: #3bb19b;
		border-top-right-radius: 10px;
		border-bottom-right-radius: 10px;
	}
	
	.right h1 {
		margin-top: 0;
		color: white;
		font-size: 40px;
		align-self: center;
	}
	
	.white_btn{
		border: none;
		outline: none;
		padding: 12px 25px;
		background-color: white;
		border-radius: 20px;
		width: auto;
	
		font-weight: bold;
		font-size: 14px;
		cursor: pointer;
		color: #3bb19b;
		transition: all 0.3s ease;
		margin-top: 1rem;
	}
	.green_btn {
		border: none;
		outline: none;
		padding: 12px 25px;
		background-color: lightblue;
		border-radius: 20px;
		width: auto;
	
		font-weight: bold;
		font-size: 14px;
		cursor: pointer;
		color: #3bb19b;
		transition: all 0.3s ease;
		margin-top: 1rem;
	}
	
	.green_btn:hover {
		background-color: darkcyan;
		color: #3bb19b;
		margin: 10px;
	}
	
	
	.white_btn:hover {
		background-color: lightblue;
		color: #3bb19b;
		margin: 10px;
	}
	
	
	}
